html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Public Sans', sans-serif;
    font-size: $font-med;
    font-weight: 300;
    color: $grayishBlue;
    line-height: 1.3;
    min-height: 300vh;

    @include breakpoint-up(large) {
        font-size: $font-med;
    }

    &.noscroll {
        overflow: hidden;
    }
}
//Text
h1, h2, h3 {
    font-weight: 300;
    line-height: 1.2;
    color: $darkBlue;
    margin-top: 0;
}

h1 {
    font-size: 1.875rem;
    margin-bottom: 1.5rem;

    @include breakpoint-up(large) {
        font-size: $font-xlarge;
    }
}

h2 {
    font-size: 1.25rem;
    margin-bottom: 1.5rem;

    @include breakpoint-up(large)
    {
    font-size: 1.875rem;
    margin-bottom: 2.25rem;
    }
}

p {
    line-height: 1.5;
    margin-bottom: 2.25rem;
}

a, a:visited, a:hover {
    text-decoration: none;
}

//Spacing
.container {
    max-width: 69.375rem;
    margin: 0 auto;

    &--pall {
        padding: 4.375rem 1.5rem;

        @include breakpoint-up(large) {
            padding-top: 6rem;
            padding-bottom: 6rem;
        }
    }

    &--y{
        padding-top: 4.375rem;
        padding-bottom: 4.375rem;

        @include breakpoint-up(large) {
            padding-top: 6rem;
            padding-bottom: 6rem;
        }
    }

    &--x {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }

    &--pt {
        padding-top: 4.375rem;

        @include breakpoint-up(large) {
            padding-top: 6rem;
        }
    }

    &--pb {
        padding-bottom: 4.375rem;

        @include breakpoint-up(large) {
            padding-bottom: 6rem;
        }
    }

    &--pl {
        padding-left: 1.5rem;
    }

    &--pr {
        padding-right: 1.5rem;
    }
}

//Flexbox
.flex {
    display: flex;

    &-jc-sb {
        justify-content: space-between;
    }

    &-jc-c {
        justify-content: center;
    }

    &-ai-c {
        align-items: center;
    }
}

//buttons
button, .button {
    display: inline-block;
    padding: 0.875rem 2.1875rem;
    background: linear-gradient(to right, $limeGreen, $brightCyan);
    border: none;
    border-radius: 50px;
    cursor: pointer;
    color: $white;
    font-weight: 400;
    font-size: $font-sm;
    transition: opacity 300ms ease-in-out;

    &:hover {
        opacity: 0.75;
    }
}

//Visibility
.hide-for-mobile {
    //hide for tablet and mobile
    @include breakpoint-down(medium) {
        display: none;
    }
}

.hide-for-desktop {
    //hide for tablet and mobile
    @include breakpoint-up(large) {
        display: none;
    }
}