.feature {
    background-color: $lightGrayishBlue;
    text-align: center;

    @include breakpoint-up(medium) {
        text-align: left;
    }

    &__intro {
        @include breakpoint-up(large) {
            width: 55%;
            margin-bottom: 3.75rem;
        }
    }

    &__grid {
        @include breakpoint-up(medium) {
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__item {
        padding: 0.9375rem;

        @include breakpoint-up(medium) {
            flex: 0 0 50%;
        }

        @include breakpoint-up(large) {
            flex: 1;
        }
    }

    &__icon {
        margin-bottom: 1.875rem;

        @include breakpoint-up(large) {
            margin-bottom: 2.75rem;
        }
    }

    &__title {
        font-weight: 300;
        line-height: 1.2;
        color: $darkBlue;
        font-size: 1.2rem;
        margin-bottom: 1rem;

        @include breakpoint-up(large) {
            margin-bottom: 1.5rem;
            font-size: 1.4rem;
        }
    }

    &__description {
        font-size: 0.875rem;
        line-height: 1.5;
    }
}